@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap);
html{line-height:1.15;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}h1{font-size:2em;margin:0.67em 0}dl dl,dl ol,dl ul,ol dl,ul dl{margin:0}ol ol,ol ul,ul ol,ul ul{margin:0}hr{box-sizing:content-box;color:inherit;height:0;overflow:visible}main{display:block}pre{font-family:monospace, monospace;font-size:1em}a{background-color:transparent}abbr[title]{text-decoration:underline;-webkit-text-decoration:underline dotted;text-decoration:underline dotted}b,strong{font-weight:bolder}code,kbd,samp{font-family:monospace, monospace;font-size:1em}small{font-size:80%}audio,video{display:inline-block}audio:not([controls]){display:none;height:0}img{border-style:none}svg:not(:root){overflow:hidden}table{border-color:inherit;text-indent:0}button,input,select{margin:0}button{overflow:visible;text-transform:none}button,[type="button"],[type="reset"],[type="submit"]{-webkit-appearance:button}fieldset{padding:0.35em 0.75em 0.625em}input{overflow:visible}legend{box-sizing:border-box;color:inherit;display:table;max-width:100%;white-space:normal}progress{display:inline-block;vertical-align:baseline}select{text-transform:none}textarea{margin:0;overflow:auto}[type="checkbox"],[type="radio"]{box-sizing:border-box;padding:0}[type="search"]{-webkit-appearance:textfield;outline-offset:-2px}::-webkit-inner-spin-button,::-webkit-outer-spin-button{height:auto}::-webkit-input-placeholder{color:inherit;opacity:0.54}::-webkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}::-moz-focus-inner{border-style:none;padding:0}:-moz-focusring{outline:1px dotted ButtonText}:-moz-ui-invalid{box-shadow:none}details{display:block}dialog{background-color:white;border:solid;color:black;display:block;height:-moz-fit-content;height:-webkit-fit-content;height:fit-content;left:0;margin:auto;padding:1em;position:absolute;right:0;width:-moz-fit-content;width:-webkit-fit-content;width:fit-content}dialog:not([open]){display:none}summary{display:list-item}canvas{display:inline-block}template{display:none}[hidden]{display:none}

body{margin:0;font-family:"Roboto Mono", monospace, "sans-serif";background-color:#434343;height:100%;width:100%}.app{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;height:100vh;width:100hw}.app .panel{display:flex;flex-direction:column;background-color:#7d7d7f;width:380px;height:680px;padding:30px 60px;border:1px solid black;border-radius:8px;box-shadow:0px 2px 6px 1px #282c34}.screen{display:flex;flex-direction:column;justify-content:space-between;color:#434343;height:220px;padding:15px;border-radius:8px;box-shadow:0px 0px 4px 0px #282c34}.screen .status-line{align-self:flex-start;color:#434343;font-size:24px;font-weight:400}.screen span.main-line{align-self:flex-end;color:#434343;font-size:48px;font-weight:500}.screen__on{background-color:#7fffff;transition:background-color 400ms linear}.screen__off{background-color:#47b2b2;transition:background-color 400ms linear}.keypad{margin-top:30px;margin-bottom:10px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:18px;padding:0px 10px}.keypad .keypad-button{font-size:48px;font-weight:500;padding:20px;color:#f3f3f3;text-align:center;background-color:#63636e;cursor:pointer;outline:none;border-radius:8px;box-shadow:0px 1px 4px 1px #282c34}

